@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}
ul,
li,
p,
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #febbcc;
  padding: 6px 12px;
  border-radius: 4px;
  color: #febbcc;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  border-radius: 20px;
  padding: 6px 20px;
}
.btn:hover {
  background: #febbcc;
  color: #fff;
}

.Home_container__3bftD {
  display: grid;
  grid-template-columns: 2fr 1fr;
  max-width: 960px;
  margin: 60px auto;
}
.Home_content__18qU0 {
  padding-right: 30px;
}
.Home_sidebar__21xz5 {
  padding-left: 30px;
}
.Home_sidebar__21xz5 form {
  padding: 20px;
  background: #febbcc;
  border-radius: 10px;
}
.Home_sidebar__21xz5 input,
.Home_sidebar__21xz5 select {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 8px;
  box-sizing: border-box;
  border: 0;
  border-radius: 4px;
  color: #555;
  font-size: 1em;
}
.Home_sidebar__21xz5 label {
  margin: 0 auto 20px;
  display: block;
  color: #fff;
}
.Home_sidebar__21xz5 button {
  color: #fff;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 90px !important;
  background-color: transparent;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  width: 100%;
}
.Home_sidebar__21xz5 h3 {
  color: #ffcccc;
  margin-bottom: 20px;
}
.Home_sidebar__21xz5 aside {
  margin-top: 40px;
  color: #555;
}
.Home_sidebar__21xz5 aside li {
  margin: 10px;
}

/* transactions */
.Home_transactions__3_tqr li {
  margin: 30px auto;
  border: 1px solid #f2f2f2;
  box-shadow: 3px 3px 5px rgba(50, 50, 50, 0.1);
  padding: 20px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-left: 4px solid #ffcccc;
}

.Home_transactions__3_tqr .Home_name__4a_Tj {
  color: #777;
  font-size: 1.4em;
}

.Home_transactions__3_tqr .Home_amount__1MW_9 {
  margin-left: auto;
  margin-right: 40px;
  color: #777;
  font-weight: bold;
  font-size: 1.6em;
}

.Home_transactions__3_tqr button {
  position: absolute;
  top: 0;
  right: 0;
  background: #ddd;
  color: #777;
  border: none;
  padding: 12px 8px;
  line-height: 0;
  font-size: 0.9em;
  cursor: pointer;
}

.Home_transactions__3_tqr .Home_confirmation__RyR2R {
  color: red;
  position: absolute;
